.navbar{
    background-color: #060b26;
    height:80px;
    width: 120%;
    display: flex;
    justify-content: start;
    align-items: center;
    position: fixed;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    color:white;
}

.nav-menu{
    background-color: #060b26;
    width: 250px;
    height: 110vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top:0;
    left:-100%;
    transition: 850ms;
}

.nav-menu.active{
    left:0;
    transition: 350ms;
}

.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    /* padding:0px; */
    list-style: none;
    height: 60px;
}

.nav-text a{
    text-decoration: none;
    color:white;
    font-size: 18px;
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover{
    background-color: teal;

}

.nav-menu-items{
    width: 100%;
}

.navbar-toggle{
    background-color: #060b26;
    width:100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span{
    margin-left:16px ;
}

