@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
*{
  margin:0;
  padding:0;
  /* overflow: hidden; */
}

.home, .reports, .products{
display: -webkit-flex;
display: flex;
height: 90vh;
-webkit-align-items: center;
        align-items: center;
-webkit-justify-content: center;
        justify-content: center;
font-size: 3rem;
}
.navbar{
    background-color: #060b26;
    height:80px;
    width: 120%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    position: fixed;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    color:white;
}

.nav-menu{
    background-color: #060b26;
    width: 250px;
    height: 110vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: fixed;
    top:0;
    left:-100%;
    transition: 850ms;
}

.nav-menu.active{
    left:0;
    transition: 350ms;
}

.nav-text{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 0px 8px 16px;
    /* padding:0px; */
    list-style: none;
    height: 60px;
}

.nav-text a{
    text-decoration: none;
    color:white;
    font-size: 18px;
    width: 80%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover{
    background-color: teal;

}

.nav-menu-items{
    width: 100%;
}

.navbar-toggle{
    background-color: #060b26;
    width:100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
}

span{
    margin-left:16px ;
}


